import {request} from '../network/request'


export function getmusic(){
    return request({
        url: '/music/get'
    })

}
export function jsonmusic(dataload){
    return request({
        url: '/music/load',
        method: 'post',
        data:JSON.stringify(dataload),
        responseType: 'json'
    })

}

export function formmusic(dataload){
    return request({
        url: '/music/load',
        method: 'post',
        headers:{
            'Content-Type':'application/x-www-form-urlencoded',
        },
        data:dataload
    })

}
export function formmusicfile(dataload){
    return request({
        url: '/music/loadmusic',
        method: 'post',
        headers:{
            'Content-Type':'application/x-www-form-urlencoded',
        },
        data:dataload
    })

}




