<template>
    <div class="fileload">
        
        <div class="contain" v-if="flag">
            
            <div class="title">
                <span>:</span>
                <input type="text" placeholder="">
            </div>
            <div class="address">
                <span>:</span>
                <input type="file" placeholder="" ref="fileadd" @change="getfile" id="filePath">
                <div class="mask" @click="middlefile">{{ filename }}</div>
            </div>
            <div class="comit">
                <button @click="comitmusic"></button>
            </div>
        </div>
        <button @click="addmusic" class="opreatmusic"></button>
        <button @click="closebar" class="opreatmusic"></button>
    </div>
</template>
<script>

import { getmusicinfo, getDuration } from 'assets/js/musicinfo.js'
import { formmusic } from '@/network/mepage.js'

export default {
    name: 'fileload',
    data() {
        return {
            flag: false,
            defaultname: "",
            filename: '',
            currentfile: {},
            musicduration: '',
            musicfile: {
                music: '',
                singer: '',
                space: '',
                time: '',
                url: '',
                img: ''
            },
            targetfile: {},
            fileform: ''
        }
    },
    created() {
        this.filename = this.defaultname

    },
    methods: {
        addmusic() {
            this.flag = true
            this.$emit('filescroll')
        },
        closebar() {
            this.flag = false

        },
        middlefile() {
            console.log('')
            this.$refs.fileadd.click()
        },
        getfile(e) {
            this.currentfile = this.$refs.fileadd.files[0]
            console.log(this.getObjectURL(this.currentfile))

            getmusicinfo(this.currentfile).then(res => {
                this.musicfile.music = res.title
                this.musicfile.singer = res.artist
                this.filename = this.musicfile.music

                var base64String = "";
                for (var i = 0; i < res.picture.data.length; i++) {
                    base64String += String.fromCharCode(res.picture.data[i]);
                }
                this.musicfile.img = "data:" + res.picture.format + ";base64," + window.btoa(base64String);
            })
            getDuration(e.currentTarget, this.currentfile).then(res => {
                this.musicfile.time = res.duration
            })
        },

        getObjectURL(file) {
            var url = null;
            if (window.createObjcectURL != undefined) {
                url = window.createOjcectURL(file);
            } else if (window.URL != undefined) {
                url = window.URL.createObjectURL(file);
            } else if (window.webkitURL != undefined) {
                url = window.webkitURL.createObjectURL(file);
            }
            return url;
        },


        comitmusic() {
            let musicform = {}
            this.musicfile.space = Math.round(this.currentfile.size / 10000) / 100 + 'MB'
            //console.log(this.musicfile)
            this.musicfile.url = this.currentfile.name
            musicform.musicinfo = JSON.stringify(this.musicfile)

            formmusic(musicform).then(res => {
                console.log(res)
                if (res.status == 200) {
                    alert("");
                    this.$toast.show('', 2000)
                }else{
                    this.$toast.show('', 2000)
                }
                
            })
        }
    }
}
</script>
<style scoped lang="less">
@media screen and (min-width : 992px) {
    .fileload {
        width: 100%;
        height: 100%;
        float: left;
        background-color: rgba(100, 100, 100, .4);
        z-index: 99;

        .contain {
            width: 800px;
            height: 500px;
            margin: 100px auto;
            display: flex;
            justify-content: space-evenly;
            flex-direction: column;
            border: 2px solid black;
            border-radius: 4px;
            transition: all .3s;
            padding: 10px;
            background-color: rgba(0, 0, 0, .3);

            div {
                text-align: left;
                color: aqua;
                font-size: 25px;
                font-family: "";
                margin-left: 20%;
            }

            .title {}

            .address {
                position: relative;

                input {
                    opacity: 0;
                    /*ie*/

                    /*  */
                    cursor: pointer;
                }

                .mask {
                    position: absolute;
                    padding: 2px;
                    height: 25px;
                    font-size: 20px;
                    line-height: 25px;
                    top: 5px;
                    left: 0px;
                    width: 300px;
                    text-align: center;
                    background-color: #fff;
                    color: black;
                    border-radius: 5px;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                }

            }

            .comit {
                margin-left: 30%;

                button {
                    width: 100px;
                    border-radius: 4px;
                    background-color: orange;
                }
            }

            .opreatmusic {
                width: 100px;
                text-align: center;
                font-size: 20px;
                border-radius: 4px;
                background-color: greenyellow;
                margin: 10px;
            }
        }
    }
}

@media screen and (max-width:767px) {
    .fileload {
        width: 100%;
        height: 100%;
        float: left;
        background-color: rgba(100, 100, 100, .4);
        z-index: 99;

        .contain {
            width: 80%;
            height: 300px;
            margin: 100px auto;
            display: flex;
            justify-content: space-evenly;
            flex-direction: column;
            border: 2px solid black;
            border-radius: 4px;
            transition: all .3s;
            padding: 10px;
            background-color: rgba(0, 0, 0, .3);

            div {
                text-align: left;
                color: aqua;
                font-size: 15px;
                font-family: "";
                margin-left: 20%;
            }

            .title {}

            .address {
                position: relative;

                input {
                    opacity: 0;
                    /*ie*/

                    /*  */
                    cursor: pointer;
                }

                .mask {
                    position: absolute;
                    padding: 2px;
                    height: 25px;
                    font-size: 15px;
                    line-height: 20px;
                    top: 0px;
                    left: 10px;
                    width: 150px;
                    text-align: center;
                    background-color: #fff;
                    color: black;
                    border-radius: 5px;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                }

            }

            .comit {
                margin-left: 30%;

                button {
                    width: 100px;
                    border-radius: 4px;
                    background-color: orange;
                }
            }

            .opreatmusic {
                width: 100px;
                text-align: center;
                font-size: 20px;
                border-radius: 4px;
                background-color: greenyellow;
                margin: 10px;
            }
        }
    }
}
</style>


